//
import moment from 'moment'
//
Vue.filter('dateFormat',function(datastr, pattern="YYYY-MM-DD hh:mm:ss"){
    return moment(datastr).format(pattern)
})

//
var car = JSON.parse(localStorage.getItem('car') || '[]');

//
var store = new Vuex.Store({
  state: {
    car:car //{id:id, count: , price: , selected: false  }
  },
  mutations:{
    //store
    addToCar(state, goodsinfo){
      var flag = false;     //car
      //storecar
      state.car.some(item =>{
        if(item.id == goodsinfo.id){
          flag = true;
          item.count += parseInt(goodsinfo.count);
          return true;
        }
      });
      //car
      if(!flag){
        state.car.push(goodsinfo);
      }
      //carcar  localStorage
      localStorage.setItem('car', JSON.stringify(state.car));
    },
    //
    updatedCount(state, data) {
      state.car.some(item =>{
        if(item.id == data.id){
          item.count = parseInt(data.count);
          return true;
        }
      })
      localStorage.setItem('car', JSON.stringify(state.car));
    },
    //
    delGoods(state, id){
      state.car.some((item, i) =>{
        if(item.id == id){
          state.car.splice(i,1);
          return true;
        }
      })
      localStorage.setItem('car', JSON.stringify(state.car));
    },
    // 
    updatedSelected(state, id){
      state.car.some(item =>{
        if(item.id == id){
          item.selected = !item.selected;
          localStorage.setItem('car', JSON.stringify(state.car));
          return true;
        }
      })
    }
  },
  getters:{
    //
    getCounts(state){
      var c = 0;
      state.car.forEach(item =>{
        c += item.count;
      });
      return c;
    },
    //
    getGoods(state){
      return state.car;
    },
    //count
    getInitCount(state){
      var count = {};
      state.car.forEach(item =>{
        count[item.id] = item.count;
      });
      return count;
    },
     // 
     getSelected(state, id){
       var c = [];
      state.car.forEach(item =>{
          c[item.id] = item.selected;
        })
      return c;
    },
    // 
    getCheckedMessage(state){
      var c ={
        count: 0, //
        money: 0  //
      }
      state.car.forEach(item =>{
        if(item.selected == true){
          c.count += item.count;
          c.money += item.price;
        }
      })
      return c;
    }
  }
})

