<template>
    <div class="comment-container">
        <h3></h3>
        <hr>
        <textarea placeholder="" maxlength="120" v-model="msg"></textarea>
        <mt-button type="primary" size="large" @click="addComment"></mt-button>

        <!--  -->
        <div class="comment-list">
            <div class="comment-item" v-for="(item, i) in comments" :key="item.add_time">
                <div class="comment-title">
                    <span>{{i+1}}</span>&nbsp;&nbsp;
                    <span>{{item.user_name}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
                    <span>:{{ item.add_time | dateFormat }}</span>
                </div>
                <div class="comment-body">
                    {{ item.content }}
                </div>
            </div>
        </div>

        <mt-button type="danger" size="large" plain @click="nextPage"></mt-button>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return{
            msg:[],
            pageIndex: 1,    //
            comments:[]      //
        };
    },
    created(){
        this.getComments();
    },
    methods:{
        //
        getComments(){
            this.$http.get("api/getcomments/" + this.id + "?pageindex=" + this.pageIndex).then(result=>{
                if(result.body.status === 0){
                    this.comments = this.comments.concat(result.body.message);
                }else{
                    Toast("");
                }
            })
        },
        //
        addComment(){
            if(this.msg == ""){
                Toast("");
            }else{
                this.$http.post("api/postcomment/" + this.$route.params.id, { content:this.msg.trim() })
                .then(result =>{
                if(result.body.status === 0){
                    Toast("");
                    //
                    var cmt = {
                        user_name: "",
                        add_time: Date.now(),
                        content: this.msg.trim()
                    };
                    this.comments.unshift(cmt);
                    this.msg = "";
                }else{
                    Toast("!");
                }
            })
            }
            
        },
        //
        nextPage(){
            this.pageIndex++;
            this.getComments();
        }
    },
    props: ["id"]
}
</script>

<style lang="scss" scoped>
    .comment-container{
        h3{
            font-size: 16px;
        }
        textarea{
            height: 80px;
        }
        .comment-list{
            font-size: 13px;
            padding: 10px 0;
            .comment-title{
                line-height: 30px;
                background-color:#ccc; 
            }
            .comment-body{
                line-height: 30px;
            }
        }
    }
</style>





